import { merge } from 'lodash-es';

import { DeepPartial } from '@yandex-music-int/ts-utility-types';

import { getPlatformSecret } from '../platform/getPlatformSecret.js';
import { TLD_MARK } from '../router/constants/tldMark.js';

import { config as defaultConfig } from './default.js';
import { getPrefixUrl } from './getPrefixUrl.js';
import { musicExternalApiRetryPolicyConfig } from './retryPolicyConfigs/musicExternalApiRetryPolicyConfig.js';
import { ConfigVariables } from './types/ConfigVariables.js';

export const config = (): ConfigVariables => {
    const BACKEND_URL = process.env.NEXT_PUBLIC_USE_PRODUCTION_BACKEND
        ? `https://api.music.yandex.${TLD_MARK}`
        : `https://api.music.qa.yandex.${TLD_MARK}`;

    return merge(defaultConfig(), {
        resources: {
            musicExternalApi: {
                prefixUrl: getPrefixUrl(BACKEND_URL),
                retryPolicyConfig: musicExternalApiRetryPolicyConfig
            }
        },
        player: {
            overembed: false,
            secretKey: getPlatformSecret(),
            externalDomain: 'localhost.music.yandex.ru',
            prefixUrl: BACKEND_URL
        },
        dev: {
            panel: true
        },
        tvm: {
            tvmtoolAuthToken: '00000000000000000000000000000000',
            host: 'http://localhost:8001'
        },
        passportCredentials: {
            host: `https://passport.yandex.${TLD_MARK}`,
            origin: 'music'
        },
        blackbox: {
            host: 'http://local-music-dev.vla.yp-c.yandex.net/bbm/blackbox'
        },
        oldWebHost: process.env.NEXT_PUBLIC_USE_PRODUCTION_BACKEND
            ? `music.yandex.${TLD_MARK}`
            : `music.qa.yandex.${TLD_MARK}`
    } satisfies DeepPartial<ConfigVariables>);
};
